import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Home from "./Home";
import Pattern from "./Pattern";
import '../css/App.css';

function App() {

  return (
    <Router>
      <div className="container">
      <Switch>
        <Route exact path="/">
          <Home/>
        </Route>
        <Route path="/pattern">
          <Pattern/>
        </Route>
      </Switch>
      </div>
    </Router>
  );
}

export default App;
