import React, { useState } from "react";
import { ChromePicker } from "react-color";
import Slider from "./Slider";
import "../css/App.css";
import "../css/Pattern.css";
import {Link} from "react-router-dom";


const toRGBA = color => `rgba(${color.r},${color.g},${color.b},1)`;

const Pattern = () => {

  const [c1, setC1] = useState({r: 36, g: 240, b: 232});
  const [c2, setC2] = useState({r: 218, g: 8, b: 191});
  const [n, setN] = useState(5);
  const [speed, setSpeed] = useState(2);

  const sendPattern = () => {
    fetch("/pattern", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({c1, c2, n, speed})
    }).then(res => console.log(res));
  };

  return (
    <div className="pattern-container">
      <div className="grid">
        <div className="picker-container">
          <p>First Color</p>
          <ChromePicker color={c1} onChange={color => setC1(color.rgb)} disableAlpha={true}/>
        </div>
        <div className="picker-container">
          <p>Second Color</p>
          <ChromePicker color={c2} onChange={color => setC2(color.rgb)} disableAlpha={true}/>
        </div>
        <div>
          <p>Repetitions {n}</p>
          <Slider value={n} min={1} max={25} step={1} onChange={setN}/>
        </div>
        <div>
          <p>Speed: {speed}</p>
          <Slider value={speed} min={1} max={10} step={1} onChange={setSpeed}/>
        </div>
      </div>
      <div className="actions">
        <button className="btn btn-nav btn-gradient" style={
          {background: `linear-gradient(to right, ${toRGBA(c1)},${toRGBA(c2)})`}
        } onClick={sendPattern}>Go!</button>
        <Link to="/" className="link-btn"><button className="btn btn-nav">Back</button></Link>
      </div>
    </div>
  )

};

export default Pattern;
