import React from "react";
import "../css/Slider.css";

const toRGBA = color => `rgba(${color.r},${color.g},${color.b},1)`;

function Slider({min, max, step, onChange, value}) {

  const startColor = {r: 150, g: 150, b: 150};
  const endColor = {r: 6, g: 214, b: 160};
  const range = max - min;
  const bgColor = toRGBA({
    r: startColor.r + (value - min) * Math.round((endColor.r - startColor.r) / range),
    g: startColor.g + (value - min) * Math.round((endColor.g - startColor.g) / range),
    b: startColor.b + (value - min) * Math.round((endColor.b - startColor.b) / range)
  });

  return (
    <div className="slider">
      <input type="range"
             style={{backgroundColor: bgColor}}
             value={value}
             onChange={event => onChange(event.target.value)}
             min={min}
             max={max}
             step={step}/>
    </div>
  )

}

export default Slider;
