import React, { useState, useEffect } from "react";
import {Subject} from "rxjs";
import io from "socket.io-client";
import {throttleTime} from "rxjs/operators";
import Switch from "./Switch";
import {SketchPicker} from "react-color";
import {Link} from "react-router-dom";
import "../css/App.css";

const onColor = new Subject();
const socket = io();

const sendColor = (rgb, cascade) => {
  socket.emit('color', {...rgb, cascade});
};

function Home() {

  const [cascade, setCascade] = useState(false);

  const sendRainbow = () => {
    fetch("/rainbow", {
      method: "GET"
    }).then(res => console.log(res))
  };

  useEffect(() => {
    const subscription = onColor.pipe(
      throttleTime(50)
    ).subscribe(rgb => sendColor(rgb, cascade));
    return () => subscription.unsubscribe();
  }, [cascade]);

  return (
      <div className="centered-container">
        <div className="settings">
          <button className="btn btn-rainbow" onClick={sendRainbow}>Rainbow</button>
          <Switch label="Cascade" isOn={cascade} handleToggle={() => setCascade(!cascade)}/>
        </div>
        <SketchPicker onChange={color => onColor.next(color.rgb)}/>
        <Link className="link-btn" to="/pattern"><button className="btn btn-nav">Pattern</button></Link>
      </div>
  );
}

export default Home;
